import React, {Fragment} from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

/*
* @ Component Imported
* */
import AboutPage from "./pages/about";
import ErrorPage from "./pages/404Error";
// import ContactPage from "./pages/contact";
import HomeDefault from "./pages/home/HomePersonalPortfolio";
import PortfolioDetailsPage from "./pages/portfolio/details";

const App = () => {
    return (
        <Fragment>
            <Router>
                <Switch>
                <Route path={`${process.env.PUBLIC_URL + "/home-default"}`}
                           component={HomeDefault}/>

                    <Route exact path={`${process.env.PUBLIC_URL + "/"}`}
                           component={HomeDefault}
                    />
{/* 
                    <Route path={`${process.env.PUBLIC_URL + "/contact"}`}
                           component={ContactPage}/> */}

                    <Route path={`${process.env.PUBLIC_URL + "/portfolio/:slug"}`}
                           component={PortfolioDetailsPage}/>
                    
                    <Route path={`${process.env.PUBLIC_URL + "/about"}`}
                           component={AboutPage}/>
                    
                    <Route exact component={ErrorPage}/>
                    
                </Switch>
            </Router>
        </Fragment>
    );
};

export default App;